import { emojiMap, emojiUrl } from './emojiMap'
import { formatDuration, isJSON } from './index'
/** message.elementSystemMessageGroupTip
 * content = {
 *  type: 'TIMTextElem',
 *  content: {
 *    text: 'AAA[]AAA[]AAA[AAA]'
 *  }
 *}
 **/

//  (opType)
const GROUP_TIP_TYPE = {
  MEMBER_JOIN: 1,
  MEMBER_QUIT: 2,
  MEMBER_KICKED_OUT: 3,
  MEMBER_SET_ADMIN: 4, // 
  MEMBER_CANCELED_ADMIN: 5, // 
  GROUP_INFO_MODIFIED: 6, // msgBody.msgGroupNewInfo.ownerAccountID
  MEMBER_INFO_MODIFIED: 7 // 
}

// text, []
function parseText (message) {
  let renderDom = []
  let temp = message.payload.text
  let left = -1
  let right = -1
  while (temp !== '') {
    left = temp.indexOf('[')
    right = temp.indexOf(']')
    switch (left) {
      case 0:
        if (right === -1) {
          renderDom.push({
            name: 'span',
            text: temp
          })
          temp = ''
        } else {
          let _emoji = temp.slice(0, right + 1)
          if (emojiMap[_emoji]) {
            renderDom.push({
              name: 'img',
              src: emojiUrl + emojiMap[_emoji]
            })
            temp = temp.substring(right + 1)
          } else {
            renderDom.push({
              name: 'span',
              text: '['
            })
            temp = temp.slice(1)
          }
        }
        break
      case -1:
        renderDom.push({
          name: 'span',
          text: temp
        })
        temp = ''
        break
      default:
        renderDom.push({
          name: 'span',
          text: temp.slice(0, left)
        })
        temp = temp.substring(left)
        break
    }
  }
  return renderDom
}
// 
function parseGroupSystemNotice (message) {
  const payload = message.payload
  const groupName =
    payload.groupProfile.name || payload.groupProfile.groupID
  let text
  switch (payload.operationType) {
    case 1:
      text = `${payload.operatorID} ${groupName}`
      break
    case 2:
      text = `${groupName}`
      break
    case 3:
      text = `${groupName}`
      break
    case 4:
      text = `${payload.operatorID}${groupName}`
      break
    case 5:
      text = `${groupName} ${payload.operatorID}`
      break
    case 6:
      text = `${payload.operatorID}${groupName}`
      break
    case 7:
      text = `${payload.operatorID}${groupName}`
      break
    case 8:
      text = `${groupName}`
      break
    case 9:
      text = `${payload.operatorID}${groupName}`
      break
    case 10:
      text = `${payload.operatorID}${groupName}`
      break
    case 255:
      text = ': ' + payload.userDefinedField
      break
  }
  return [{
    name: 'system',
    text: text
  }]
}
// 
function parseGroupTip (message) {
  const payload = message.payload
  const userName = message.nick || payload.userIDList.join(',')
  let tip
  switch (payload.operationType) {
    case GROUP_TIP_TYPE.MEMBER_JOIN:
      tip = `${userName}`
      break
    case GROUP_TIP_TYPE.MEMBER_QUIT:
      tip = `${userName}`
      break
    case GROUP_TIP_TYPE.MEMBER_KICKED_OUT:
      tip = `${userName}`
      break
    case GROUP_TIP_TYPE.MEMBER_SET_ADMIN:
      tip = `${payload.operatorID} ${userName}`
      break
    case GROUP_TIP_TYPE.MEMBER_CANCELED_ADMIN:
      tip = `${payload.operatorID} ${userName}`
      break
    case GROUP_TIP_TYPE.GROUP_INFO_MODIFIED:
      tip = ''
      break
    case GROUP_TIP_TYPE.MEMBER_INFO_MODIFIED:
      for (let member of payload.memberList) {
        if (member.muteTime > 0) {
          tip = `${member.userID}${member.muteTime}`
        } else {
          tip = `${member.userID}`
        }
      }
      break
  }
  return [{
    name: 'groupTip',
    text: tip
  }]
}
// 
function parseCustom (message) {
  let data = message.payload.data
  if (isJSON(data)) {
    data = JSON.parse(data)
    if (data.hasOwnProperty('version') && data.version === 3) {
      let tip
      const time = formatDuration(data.duration)
      switch (data.action) {
        case -2:
          tip = ''
          break
        case 0:
          tip = ''
          break
        case 1:
          tip = ''
          break
        case 2:
          tip = ''
          break
        case 3:
          tip = ''
          break
        case 4:
          tip = ''
          break
        case 5:
          if (data.duration === 0) {
            tip = ''
          } else {
            tip = `${time}`
          }
          break
        case 6:
          tip = ''
          break
      }
      return [{
        name: 'videoCall',
        text: tip
      }]
    }
  }
  return [{
    name: 'custom',
    text: data
  }]
}
// message element
export function decodeElement (message) {
  // renderDom
  switch (message.type) {
    case 'TIMTextElem':
      return parseText(message)
    case 'TIMGroupSystemNoticeElem':
      return parseGroupSystemNotice(message)
    case 'TIMGroupTipElem':
      return parseGroupTip(message)
    case 'TIMCustomElem':
      return parseCustom(message)
    default:
      return []
  }
}
